@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&display=swap);
.nav{display:flex;justify-content:space-between;align-items:center;position:-webkit-sticky;position:sticky;padding:0.6rem 1rem;font-size:x-large;margin:0.5rem 0}.nav::after{content:'';position:absolute;top:0;left:0;top:0;left:0;right:0;bottom:0;box-shadow:inset 0 0 2000px rgba(255,255,255,0.4);z-index:1}.nav>*{z-index:2}.nav ul{list-style:none}.nav ul li{padding:0 0.7rem;display:inline;cursor:pointer}.nav ul li *{transition:all 0.5s ease}.nav ul li *:hover{color:#128a6c}

.modal-dialog{position:absolute;top:30%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);padding:2rem}.modal-dialog::before{content:'';background-color:rgba(49,214,173,0.5);position:absolute;top:0;left:0;right:0;bottom:0;-webkit-filter:blur(5px);filter:blur(5px);z-index:-5}.form-label{font-size:20px}.form-control{border:none;padding:0.5rem;margin:0.5rem}.select{border-radius:5px;width:50%;background-color:#3f3f3f;cursor:pointer;-webkit-appearance:none;-moz-appearance:none;appearance:none;line-height:1.7;background-image:linear-gradient(45deg, transparent 50%, gray 50%),linear-gradient(135deg, gray 50%, transparent 50%),linear-gradient(to right, #ccc, #ccc);background-position:calc(100% - 20px) calc(1em + 2px),calc(100% - 15px) calc(1em + 2px),calc(100% - 2.5em) 0.5em;background-size:5px 5px, 5px 5px, 1px 1.5em;background-repeat:no-repeat}.btn{border-radius:5px;font-size:24px;border:none;padding:0.25rem 0.75rem;transition:0.1s ease;cursor:pointer}.btn:hover{-webkit-transform:scale(1.02);transform:scale(1.02)}.btn-primary{background-color:#3f3f3f}.btn-secondary{background-color:#128a6c}.btn-dark{background-color:#043d2f}

.quickstart-flex{display:flex;justify-content:space-between;align-items:center}

.card{background-color:#128a6c;border-radius:4px;width:100%;min-width:80px;max-width:200px;padding:0.5rem 0}.wide-btn{max-width:300px;padding:0.5rem 0;margin:0 auto}

*,body,html{font-family:'Roboto', sans-serif;margin:0;padding:0;box-sizing:border-box;color:white;overflow:hidden}#bg{position:absolute;top:0;left:0;width:100%;height:100%;background-color:gray;background-image:url("https://source.unsplash.com/1600x900/?nature,forest");background-position:top;background-attachment:fixed;background-repeat:no-repeat;background-size:cover;-webkit-transform:scale(1.1);transform:scale(1.1);-webkit-filter:blur(4px) brightness(0.5);filter:blur(4px) brightness(0.5);z-index:-10}h1{font-size:3rem}a{text-decoration:none}.mt{margin-top:1rem}.mt-big{margin-top:2.5rem}.mb{margin-bottom:1rem}.mb-big{margin-bottom:2.5rem}.container{width:80%;max-width:80%;margin:0 auto;padding:1rem}.grid{display:grid;grid-gap:50px 50px;justify-items:center;grid-template-columns:repeat(2, 1fr)}@media all and (min-width: 960px){.grid{grid-template-columns:repeat(3, 1fr)}}.text-center{text-align:center}

